.game-state, .game-state td, .game-state th {
    width: 360px;
    border: 1px solid white;
    border-collapse: collapse;
    text-align: center;
    padding: 10px;
    background-color: rgb(75, 75, 75);
    color: white;
    box-shadow: 0px 0px 5px black;
}

.game-state td {
    width: 25%;
}